
.Selected-clip-attribute-edit {
    width: 450px;
    display: inline-block;
}

.List-visibility-checkboxes {
    width: 450px;
    display: inline-block;
}

.Attribute-edit-col-left {
    width: 30%;
    display: inline-block;
}

.Attribute-edit-col-left p {
    float: right;
    margin-right: 10px;
}

.Attribute-edit-col-right {
    width: 50%;
    display: inline-block;
}

.Attribute-edit-col-right p {
    float: left;
}

.Attribute-edit-col-right input {
    float: left;
    margin-bottom: 16px;
}

.Toggle-w-text {
    float: left;
    
}

.Toggle-w-text p {
    margin-left: 10px;
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-40%);
        -ms-transform: translateY(-40%);
            transform: translateY(-40%);
}


